body,
main {
  background: #f2c14e; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to bottom,
    #f2c14e,
    #f78154,
    #f2c14e,
    #f78154
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(#f2c14e),
    color-stop(#f78154),
    color-stop(#f2c14e),
    to(#f78154)
  );
  background: -webkit-linear-gradient(
    top,
    #f2c14e,
    #f78154,
    #f2c14e,
    #f78154
  );
  background: -o-linear-gradient(
    top,
    #f2c14e,
    #f78154,
    #f2c14e,
    #f78154
  );
  background: linear-gradient(
    to bottom,
    #f2c14e,
    #f78154,
    #f2c14e,
    #f78154
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background-repeat: no-repeat;
  background-size: 100% 2400px;
  -webkit-font-smoothing: antialiased;
}

@media (min-width: 1100px) {
  body {
    cursor: url('../src/assets/cursor/arrow.cur'), auto;
  }
}

@media (min-width: 1400px) {
  body {
    height: 100vh;
  }
}

@media (min-width: 2000px) {
  body,
  main {
    background-size: 100% 300vh;
  }
}

@-webkit-keyframes introbackground {
  0% {
    background: #1488cc;
    background-size: cover;
  }
  100% {
    background: transparent;
    background-size: cover;
  }
}

@keyframes introbackground {
  0% {
    background: #1488cc;
    background-size: cover;
  }
  100% {
    background: transparent;
    background-size: cover;
  }
}

.intro-bodyBackground {
  overflow: hidden;
  max-width: 100vw;
  background: #1488cc;
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-animation-name: introbackground;
          animation-name: introbackground;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@media (min-width: 1400px) {
  .intro-bodyBackground {
    height: 100vh;
  }
}
