@-webkit-keyframes aparecerDevices {
  0% {
    opacity: 0;
    display: none;
    -webkit-transform: rotate(90deg) scale(2) translateY(-50%) translateX(50%);
            transform: rotate(90deg) scale(2) translateY(-50%) translateX(50%);
  }

  50% {
    opacity: 0;
    display: initial;
  }

  100% {
    opacity: 1;
    -webkit-transform: rotate(0deg) scale(1) translateY(0%) translateX(0%);
            transform: rotate(0deg) scale(1) translateY(0%) translateX(0%);
  }
}

@keyframes aparecerDevices {
  0% {
    opacity: 0;
    display: none;
    -webkit-transform: rotate(90deg) scale(2) translateY(-50%) translateX(50%);
            transform: rotate(90deg) scale(2) translateY(-50%) translateX(50%);
  }

  50% {
    opacity: 0;
    display: initial;
  }

  100% {
    opacity: 1;
    -webkit-transform: rotate(0deg) scale(1) translateY(0%) translateX(0%);
            transform: rotate(0deg) scale(1) translateY(0%) translateX(0%);
  }
}

/* 
DEFAULT VERSION
DESIGNED FOR MOBILE, TABLET AND SMALL DESKTOP
*/
.animation {
  display: inline-block;
  position: relative;
  width: 378px;
  height: 290px;
  margin: 0 auto;
  vertical-align: middle;
  font-size: 18px;
  opacity: 0;
  -webkit-animation-name: aparecerDevices;
          animation-name: aparecerDevices;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
}

@media (max-width: 500px) {
  .animation {
    margin: 0 0;
  }
}

.animation .device {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 50%;
  top: 50%;
  background: #111;
  -webkit-transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  -o-transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  -ms-transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.animation .device .phone-home-button,
.animation .device .tablet-home-button {
  position: absolute;
  border-radius: 50%;
  background: #444;
  opacity: 0;
  z-index: 1;
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}
.animation .device .phone-home-button {
  width: 16px;
  height: 16px;
  margin-top: -8px;
  right: 11px;
  top: 50%;
}
.animation .device .tablet-home-button {
  width: 12px;
  height: 12px;
  margin-left: -6px;
  bottom: 7px;
  left: 50%;
}
.animation .device .screen-stand {
  position: absolute;
  width: 100%;
  margin-left: -10px;
  margin-top: -1px;
  top: 60%;
  opacity: 0;
  z-index: 1;
  -webkit-transition: all 0.4s ease-out;
  -o-transition: all 0.4s ease-out;
  transition: all 0.4s ease-out;
}
.animation .device .screen-stand .leg {
  position: absolute;
  width: 12px;
  height: 16px;
  left: 50%;
  top: 0;
  margin-left: -6px;
  background: #111;
}
.animation .device .screen-stand .foot {
  position: absolute;
  width: 120px;
  height: 4px;
  left: 50%;
  top: 15px;
  margin-left: -60px;
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
  background: #111;
}
.animation .device .display {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: #34495e;
  z-index: 3;
}
.animation .device .display div {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 100%;
  white-space: nowrap;
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}
.animation .device .display div div {
  position: absolute;
  width: 100%;
  left: 0;
  top: 50%;
  margin-top: -28px;
  font-size: 1.26em;
  text-align: center;
  color: gold;
  font-weight: 800;
  opacity: 0.75;
}

.animation .device .display div div em {
  font-weight: bold;
}
.animation .device .display .slide1 {
  background: #da4453; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #89216b,
    #da4453
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#89216b),
    to(#da4453)
  );
  background: -webkit-linear-gradient(
    left,
    #89216b,
    #da4453
  );
  background: -o-linear-gradient(
    left,
    #89216b,
    #da4453
  );
  background: linear-gradient(
    to right,
    #89216b,
    #da4453
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.animation .device .display .slide2 {
  background: #da4453; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #89216b,
    #da4453
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#89216b),
    to(#da4453)
  );
  background: -webkit-linear-gradient(
    left,
    #89216b,
    #da4453
  );
  background: -o-linear-gradient(
    left,
    #89216b,
    #da4453
  );
  background: linear-gradient(
    to right,
    #89216b,
    #da4453
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.animation .device .display .slide3 {
  background: #da4453; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #89216b,
    #da4453
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#89216b),
    to(#da4453)
  );
  background: -webkit-linear-gradient(
    left,
    #89216b,
    #da4453
  );
  background: -o-linear-gradient(
    left,
    #89216b,
    #da4453
  );
  background: linear-gradient(
    to right,
    #89216b,
    #da4453
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.animation[data-animation-step='1'] .device {
  width: 70%;
  height: 70%;
  padding: 10px;
  border-radius: 4px;
}
.animation[data-animation-step='1'] .device .slide1 {
  left: 0%;
}
.animation[data-animation-step='1'] .device .screen-stand {
  opacity: 1;
  top: 100%;
}
.animation[data-animation-step='2'] .device {
  width: 45%;
  height: 82%;
  padding: 24px;
  border-radius: 10px;
}
.animation[data-animation-step='2'] .device .slide1 {
  left: -100%;
}
.animation[data-animation-step='2'] .device .slide2 {
  left: 0%;
}
.animation[data-animation-step='2'] .device .tablet-home-button {
  opacity: 1;
}
.animation[data-animation-step='3'] .device {
  width: 60%;
  height: 42%;
  padding: 10px 36px;
  border-radius: 6px;
}
.animation[data-animation-step='3'] .device .slide1,
.animation[data-animation-step='3'] .device .slide2 {
  left: -100%;
}
.animation[data-animation-step='3'] .device .slide3 {
  left: 0%;
}
.animation[data-animation-step='3'] .device .phone-home-button {
  opacity: 1;
}

/* BIG SCREENS VERSION */

@media (min-width: 1400px) and (max-width: 1999px) {
  .animation {
    font-size: 1.1vw;
    width: 25.2vw;
    height: 19.33vw;
    line-height: 3vh;
  }
}

@media (min-width: 2000px) {
  .animation {
    font-size: 1.6vw;
    width: 37.8vw;
    height: 29vw;
    line-height: 3vw;
  }

  .animation .device .phone-home-button {
    width: 32px;
    height: 32px;
    margin-top: -16px;
    right: 22px;
  }
  .animation .device .tablet-home-button {
    width: 24px;
    height: 24px;
    margin-left: -12px;
    bottom: 14px;
  }
  .animation .device .screen-stand {
    margin-left: -20px;
    margin-top: -2px;
  }

  .animation .device .screen-stand .leg {
    width: 24px;
    height: 32px;
    margin-left: -12px;
  }
  .animation .device .screen-stand .foot {
    width: 240px;
    height: 8px;
    top: 30px;
    margin-left: -120px;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
  }

  .animation .device .display div div {
    margin-top: -56px;
    font-size: 1.26em;
  }

  .animation[data-animation-step='1'] .device {
    padding: 20px;
    border-radius: 8px;
  }

  .animation[data-animation-step='2'] .device {
    padding: 48px;
    border-radius: 20px;
  }

  .animation[data-animation-step='3'] .device {
    padding: 20px 72px;
    border-radius: 12px;
  }
}

