.react-screen-test {
  position: relative;
  width: 100%;
  height: 100vh;
  background: #888;
}
.react-screen-wrapper.out .react-screen,
.react-screen-wrapper.out .react-screen-background {
  z-index: -1;
}

.react-screen-wrapper .hide {
  display: none;
}

.react-screen-skip {
  position: fixed;
  z-index: 99999;
  bottom: 20px;
  right: 10px;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  text-align: center;
  font-size: 1rem;
  color: #999;
  padding: 0.5rem 2rem;
  background-color: #f5f5f5;
  letter-spacing: 1px;
  border-radius: 5px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease;
  text-decoration: none;
}

.react-screen-skip:hover {
  cursor: pointer;
  background-color: #eee;
  color: #666;
  text-decoration: none;
}

/* IMPORTANTEEE ACA ESTA EL BACKGROUND DE TODA ESTA MIERDA */
.react-screen {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 99998;
  -webkit-font-smoothing: antialiased;
}

/* Reactscreen tiene un background de dos capas
Este background esta debajo del background anterior */
.react-screen-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 99998;
  background: white;
  background-size: 100% 100%;
  -webkit-font-smoothing: antialiased;
}

