.react-screen-progress {
  visibility: hidden;
  position: fixed;
  right: 30px;
  top: 50%;
  width: 2px;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  z-index: 99999;
}

.react-screen-progress .item {
  position: relative;
  width: 2px;
  height: 40px;
  background: #ddd;
}

.react-screen-progress .item .number {
  position: absolute;
  right: 15px;
  line-height: 40px;
  color: #ddd;
}

.react-screen-progress .item .number.active {
  color: #777;
}

.react-screen-progress .item .number:hover {
  cursor: pointer;
}

.react-screen-progress .bar {
  position: absolute;
  top: 0;
  left: 0;
  width: 2px;
  height: 50%;
  background: #777;
}

