/* Default Config - Mobile First Designed*/
#android-bot {
  width: 175px;
  margin: 0 auto;
  position: relative;
}

.antenna,
#head,
#android-torso,
.arm,
.leg {
  background: #a4c739;
}

#antennas {
  width: 100px;
  height: 25px;
  margin: 0 auto;
}

.antenna {
  width: 2.5px;
  height: 25px;
  border-radius: 2.5px;
  -webkit-border-radius: 2.5px;
  -moz-border-radius: 2.5px;
  -ms-border-radius: 2.5px;
  -o-border-radius: 2.5px;
  float: left;
  position: relative;
  top: 10px;
}

#antenna-left {
  left: 17.5px;
  transform: rotate(-33deg);
  -webkit-transform: rotate(-33deg);
  -moz-transform: rotate(-33deg);
  -ms-transform: rotate(-33deg);
  -o-transform: rotate(-33deg);
}

#antenna-right {
  left: 77px;
  transform: rotate(33deg);
  -webkit-transform: rotate(33deg);
  -moz-transform: rotate(33deg);
  -ms-transform: rotate(33deg);
  -o-transform: rotate(33deg);
}

#head {
  width: 100px;
  margin: 0 auto;
  height: 50px;
  border-radius: 50px 50px 0 0;
  -webkit-border-radius: 50px 50px 0 0;
  -moz-border-radius: 50px 50px 0 0;
  -ms-border-radius: 50px 50px 0 0;
  -o-border-radius: 50px 50px 0 0;
  margin-bottom: 2.5px;
}

.vision {
  width: 7.5px;
  height: 7.5px;
  border-radius: 3.5px;
  -webkit-border-radius: 3.5px;
  -moz-border-radius: 3.5px;
  -ms-border-radius: 3.5px;
  -o-border-radius: 3.5px;
  background-color: white;
  float: left;
  position: relative;
  top: 25px;
}

#vision-left {
  left: 22.5px;
}

#vision-right {
  left: 62.5px;
}

.android-body {
  width: 170px;
  height: 100px;
  margin: 0 auto;
  text-align: center;
}

#upper.android-body {
  position: relative;
}

#android-torso {
  position: relative;
  width: 100px;
  height: 100px;
  float: left;
  border-radius: 0 0 12.5px 12.5px;
  -webkit-border-radius: 0 0 12.5px 12.5px;
  -moz-border-radius: 0 0 12.5px 12.5px;
  -ms-border-radius: 0 0 12.5px 12.5px;
  -o-border-radius: 0 0 12.5px 12.5px;
  margin: 0 3px;
}

@media (max-width: 1199px) {
  #android-text {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    text-align: center;
    margin: auto;
  }
  #android-text img {
    max-width: 90%;
    height: auto;
    margin-left: 12%;
    margin-right: 12%;
    margin-top: auto;
    margin-bottom: auto;
    padding-top: 5px;
    padding-bottom: 5px;
  }
}

.arm {
  width: 25px;
  height: 81px;
  float: left;
  border-radius: 25px;
  -webkit-border-radius: 25px;
  -moz-border-radius: 25px;
  -ms-border-radius: 25px;
  -o-border-radius: 25px;
}

#arm-left {
  margin-left: 7px;
}

#arm-right {
  -webkit-animation: wave 2s ease-in-out infinite;
  animation: wave 2s ease-in-out infinite;
  top: -65px;
  right: 0px;
  position: absolute;
}

@-webkit-keyframes wave {
  0% {
    -webkit-transform: rotate(15deg) translate(0px);
    transform: rotate(15deg) translate(0px);
  }
  50% {
    -webkit-transform: rotate(45deg) translate(15px);
    transform: rotate(45deg) translate(15px);
  }
  100% {
    -webkit-transform: rotate(15deg) translate(0px);
    transform: rotate(15deg) translate(0px);
  }
}

@keyframes wave {
  0% {
    -webkit-transform: rotate(15deg) translate(0px);
    transform: rotate(15deg) translate(0px);
  }
  50% {
    -webkit-transform: rotate(45deg) translate(15px);
    transform: rotate(45deg) translate(15px);
  }
  100% {
    -webkit-transform: rotate(15deg) translate(0px);
    transform: rotate(15deg) translate(0px);
  }
}

#lower.android-body {
  width: 100px;
  height: 50px;
  margin: 0 auto;
}

.leg {
  width: 25px;
  height: 50px;
  float: left;
  border-radius: 0 0 25px 25px;
  -webkit-border-radius: 0 0 25px 25px;
  -moz-border-radius: 0 0 25px 25px;
  -ms-border-radius: 0 0 25px 25px;
  -o-border-radius: 0 0 25px 25px;
}

#leg-left {
  margin-left: 19px;
}

#leg-right {
  margin-left: 13px;
}

